import axios from "axios";
import {baseURL,baseURLroom} from '../index'


export const getHotels = (style,id) =>{
    let URL = baseURL
    switch (style) {
        case 'allHotels' ://
            URL = baseURL
            break

        case 'onceHotels' ://
            URL = baseURLroom+'/'+id
            break
    }


    return axios.get(URL)
        .then(function (response) {
            return response.data
        })
        .catch(function (error) {
            console.log(error);
        });


}

export const checkHotels = (id,param) => {//
    return axios.post(baseURLroom+'/'+id,param)
        .then(function (response) {

            return response.data

        })
        .catch(function (error) {
            console.log(error);
        });


}




export const deleteHotels = () => {//
    return axios.delete(baseURL)
        .then(function (response) {
            return response.data
        })
        .catch(function (error) {
            console.log(error);
        })


}


